@charset "utf-8";

/* layout */
.common-inner {width: calc($inner-width + $inner-gutter * 2);margin: 0 auto;padding: 0 $inner-gutter;}

/* search */
.common-search-layout #container {padding: 20px 0 100px;}

.common-search-form {}
.common-search-form .form-area {padding: 20px;border-radius: 10px;background: $grey200;}
.common-search-form .form-keyword {position: relative;}
.common-search-form .form-keyword-input {@include font-mid;padding: 8px 10px;padding-left: calc(24px + 10px * 2);}
.common-search-form .form-keyword-input:focus + .form-keyword-icon {color: $primary;}
.common-search-form .form-keyword-icon {position: absolute;left: 10px;top: 50%;transform: translate(0, -50%);color: $grey400;transition: color $transition-speed;}
.common-search-form .form-filter:not(:first-child) {margin-top: 20px;}
.common-search-form .form-filter-title {margin-bottom: 10px;}
.common-search-form .form-filter-title p {@include font-mid;color: $grey900;font-weight: $font-bold;}
.common-search-form .form-filter-row {display: flex;align-items: center;gap: 10px;}
.common-search-form .form-filter-row:not(:last-child) {margin-bottom: 10px;}
.common-search-form .form-filter-label {@include font-base;display: inline-block;width: 120px;color: $grey;font-weight: $font-medium;}
.common-search-form .form-filter-radio-row {display: flex;align-items: center;gap: 20px;}
.common-search-form .form-filter-radio.type-detail {padding-right: 20px;}
.common-search-form .form-filter-radio.type-detail::after {content: '\ea4e';font-family: $font-icon;display: inline-block;position: absolute;right: 0;top: 50%;transform: translate(0, -50%);color: $grey400;font-size: $font-mid;transition: transform $transition-speed;}
.common-search-form .form-filter-radio.type-detail.active-detail::after {transform: translate(0, -50%) rotate(180deg);}
.common-search-form .form-filter-input {width: 700px;}
.common-search-form .form-filter-date-range {border-color: $grey400;border-radius: 8px;}
.common-search-form .form-filter-date-range .ant-picker-input > input {text-align: center;}
.common-search-form .form-filter-check-row {display: flex;align-items: center;gap: 20px;}
.common-search-form .form-button-row {display: flex;justify-content: center;margin-top: 40px}
.common-search-form .form-button-submit {width: 180px;}

.common-search-result {margin-top: 50px;}
.common-search-result .result-area {display: flex;gap: 20px;}
.common-search-result .result-area1 .result-col1 {width: 30%;}
.common-search-result .result-area1 .result-col2 {width: 70%;}
.common-search-result .result-table tbody tr {transition: background $transition-speed;cursor: pointer;}
.common-search-result .result-table tbody tr:hover {background: rgba($primary, 0.1);}
.common-search-result .result-table tbody tr.active {background: $primary100;pointer-events: none;}
.common-search-result .result-table tbody tr.active td {font-weight: $font-bold;}
.common-search-result .result-document-title {padding: 6px 10px;border: 1px solid $grey300;border-left: 0;border-right: 0;background: $grey100;text-align: center;}
.common-search-result .result-document-title p {@include font-base;color: $grey900;font-weight: $font-bold;}
.common-search-result .result-document-list > li {padding: 6px 6px 12px;border-bottom: 1px solid $grey300;}
.common-search-result .result-document-list .list-title {margin-bottom: 5px;}
.common-search-result .result-document-list .list-title a {@include font-mid;display: inline-block;color: $grey800;font-weight: $font-bold;text-decoration: underline;transition: color $transition-speed;}
.common-search-result .result-document-list .list-title a:hover {color: $primary;}
.common-search-result .result-document-list .list-text {position: relative;}
.common-search-result .result-document-list .list-text.cut::after {content: '';display: block;position: absolute;left: 0;bottom: 0;width: 100%;height: calc($font-small * $line-height2 * 1.5);background: linear-gradient(180deg, transparent 0%, #FFF 100%);}
.common-search-result .result-document-list .list-text.cut p {@include text-cut-line($line-height2, 3);}
.common-search-result .result-document-list .list-text.cut.open {padding-bottom: 20px;}
.common-search-result .result-document-list .list-text.cut.open::after {display: none;}
.common-search-result .result-document-list .list-text.cut.open p {@include text-cut-clear;}
.common-search-result .result-document-list .list-text.cut.open .list-text-button-more {transform: translate(-50%, 0) rotate(180deg);}
.common-search-result .result-document-list .list-text.cut.open .list-text-button-more:hover {transform: translate(-50%, -4px) rotate(180deg);}
.common-search-result .result-document-list .list-text p {@include font-small;color: $grey;}
.common-search-result .result-document-list .list-text-button-more {z-index: 5;display: inline-flex;justify-content: center;align-items: center;position: absolute;bottom: 0;left: 50%;transform: translate(-50%, 0);width: 20px;height: 20px;border: 1px solid $primary;border-radius: 50%;background: $white;color: $primary;transition: transform $transition-speed;cursor: pointer;}
.common-search-result .result-document-list .list-text-button-more:hover {transform: translate(-50%, 4px);}
.common-search-result .result-document-list .list-text-button-more .more-icon {flex-shrink: 0;}