@charset "utf-8";

.vtable {}
.vtable.text-center,
.vtable .text-center {text-align: center;}
.vtable.text-right,
.vtable .text-right {text-align: right;}
.vtable thead {border: 1px solid $grey300;border-left: 0;border-right: 0;background: $grey100;}
.vtable th,
.vtable td {@include font-base;padding: 6px 10px;}
.vtable th {color: $grey900;font-weight: $font-bold;}
.vtable td {color: $grey;}
.vtable tbody tr {border-bottom: 1px solid $grey300;}